import { useEffect, useState } from "react";
import { dataSource } from "../api/datasources";
import {fetchData} from "../api/fetchData";
import "../types";



const initializeNumber:NumberInfo= {
_id: "",
comments: [],
prefix: 0,
location: "",
totalSearch:0,
spamReports:0,
owner: "",
lineType:"",
originWebsite:"",
whatsapp:false,
number: "",
}

export default function useNumbers() {
    const [numbersInfo, setNumbersInfo] = useState([initializeNumber])

    const updateNumbers = async () => {
        try {
            const apiPromise = await fetchData(`${dataSource}/number/getAll`);
            const apiResponse = await apiPromise.json();
            const response = apiResponse.response  as NumberInfo[];
            setNumbersInfo(response)
        } catch (err) {
            console.log(err)
        }
    }

    useEffect(() => {
        updateNumbers()
    }, [])

    return { numbersInfo, updateNumbers }
}