import { adminDataSource, dataSource } from "../api/datasources";
import { deleteData, postDataWithoutPayload, putData } from "../api/fetchData";
import useCommentsToModerate from "../customHooks/useCommentsToModerate";
import { useEffect, useState } from "react";
import AdminSearch from "./adminSearch";
import TopNav from "./topNav";
import EscortAlert from "./alert";
import getToken from "../utils/getToken";

export default function AdminComments({ setActivePage }: GeneralAdmin) {
  const { comments, updateComments } = useCommentsToModerate();

  //Comments to be filtered and show as results of search
  const [filteredComments, setFilteredComments] = useState(comments);

  //Info of the comment selected by the user in the table of comments
  const [selectedComment, setSelectedComment] = useState(comments[0]);

  //alert banner
  const [alert, setAlert] = useState({
    state: true,
    color: "primary_theme",
    text: "Tus alertas aparecern en esta seccin",
    icon: "notifications",
  });

  //Admin is editing
  const [adminEditing, setAdminEditing] = useState(false);
  const handleModeration = async (commentId: string, parentNumber: string) => {
    try {
      await postDataWithoutPayload(
        `${adminDataSource}/comment/${commentId}/parent/${parentNumber}/accept`,
        getToken()
      );
      await updateComments();
    } catch (err) {
      console.log(err);
    }
  };

  const handleCommentDeletion = async (commentId: string) => {
    try {
      await deleteData(
        `${adminDataSource}/comment/${commentId}/deleteModerate`,
        getToken()
      );
      updateComments();
    } catch (err) {
      console.log(err);
    }
  };

  const showComment = (commentId: string) => {
    //Set the visibility of editor
    setAdminEditing(true);
    const userSelectedComment = comments.find(
      (comment) => comment._id === commentId
    );
    if (userSelectedComment) {
      setSelectedComment(userSelectedComment);
    }
  };

  const handleChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const value = evt.target.value;
    const id = evt.target.id;
    setSelectedComment((prevState) => ({ ...prevState, [id]: value }));
  };

  const handleSubmit = async (evt: React.FormEvent<HTMLFormElement>) => {
    evt.preventDefault();
    const selectedCommentInfo = new FormData();
    selectedCommentInfo.append("commentInfo", JSON.stringify(selectedComment));
    const apiResponse = await putData(
      `${adminDataSource}/comment/${selectedComment._id}/update`,
      selectedCommentInfo,
      getToken()
    );

    if (apiResponse.status === 200) {
      setAlert({
        state: true,
        color: "primary_theme",
        text: "Los cambios han sido guardados",
        icon: "notifications",
      });
    }
    if (apiResponse.status === 400) {
      setAlert({
        state: true,
        color: "primary_theme",
        text: "Ha ocurrido un error: #400, intente nuevamente",
        icon: "danger",
      });
    }
    updateComments();
  };

  //update filtered Elements
  useEffect(() => {
    setFilteredComments(comments);
  }, [comments]);

  let tableContent: JSX.Element;

  if (filteredComments.length === 0) {
    tableContent = (
      <tbody>
        <tr>
          <td colSpan={6}>No hay comentarios para moderar...</td>
        </tr>
      </tbody>
    );
  } else {
    tableContent = (
      <tbody>
        {filteredComments.map((comment) => {
          return (
            <tr key={comment._id} onClick={() => showComment(comment._id)}>
              <td>{comment.commentUsername}</td>
              <td>{comment.commentText}</td>
              <td>{comment.parentNumber}</td>
              <td>
                <div
                  className="btn_small success_theme"
                  onClick={() =>
                    handleModeration(comment._id, comment.parentNumber)
                  }
                >
                  Aceptar
                </div>
              </td>
              <td>
                <div
                  className="btn_small escort_theme"
                  onClick={() => handleCommentDeletion(comment._id)}
                >
                  Rechazar
                </div>
              </td>
            </tr>
          );
        })}
      </tbody>
    );
  }

  return (
    <section className="comments_moderation">
      <EscortAlert
        text={alert.text}
        color={alert.color}
        state={alert.state}
        icon={alert.icon}
        action={setAlert}
      />
      <TopNav title="COMENTARIOS" setActivePage={setActivePage}></TopNav>

      <main className="escort_admin_page">
        <AdminSearch
          propToSearch="commentText"
          originalArray={comments}
          setFiltered={setFilteredComments}
        />
        <div className="table_container">
          <table className="escorts_table my_sm_16">
            <thead>
              <tr>
                <td>Usuario</td>
                <td>Comentario</td>
                <td>Nmero</td>
                <td></td>
                <td></td>
              </tr>
            </thead>
            {tableContent}
          </table>
        </div>
        <div
          className={`admin_editor ${adminEditing ? "show_container" : "hide"}`}
        >
          <div className="editor_header">
            <h2 className="txt_white">Editar comentario</h2>
            <div
              className="close_editor"
              onClick={() => setAdminEditing(false)}
            >
              <span className="material-symbols-outlined">cancel</span>
            </div>
          </div>
          <form className="escorts_form" onSubmit={handleSubmit}>
            <div className=" input_field_sm">
              <label htmlFor="commentUsername">Usuario</label>
              <input
                type="text"
                id="commentUsername"
                value={selectedComment.commentUsername}
                onChange={handleChange}
              />
            </div>
            <div className=" input_field_sm">
              <label htmlFor="commentText">Comentario</label>
              <input
                type="text"
                id="commentText"
                value={selectedComment.commentText}
                onChange={handleChange}
              />
            </div>
            <button className="btn escort_theme">Modificar</button>
          </form>
        </div>
      </main>
    </section>
  );
}
