import React, { useEffect, useState } from "react";
import { adminDataSource, dataSource } from "../api/datasources";
import { deleteData, postDataWithoutPayload, putData } from "../api/fetchData";
import useNumbers from "../customHooks/useNumbers";
import useNumbersToModerate from "../customHooks/useNumbersToModerate";
import getToken from "../utils/getToken";
import AdminSearch from "./adminSearch";
import TopNav from "./topNav";

export default function AdminEscortNumber({ setActivePage }: GeneralAdmin) {
  //Variables
  let numberTable: JSX.Element;

  const { numbersInfo, updateNumbers } = useNumbers();

  const [filteredNumbers, setFilteredNumbers] = useState(numbersInfo);

  //Info of the comment selected by the user in the table of comments
  const [selectedNumber, setSelectedNumber] = useState(numbersInfo[0]);
  //Admin is editing
  const [adminEditing, setAdminEditing] = useState(false);

  //update filtered Elements
  useEffect(() => {
    setFilteredNumbers(numbersInfo);
  }, [numbersInfo]);

  const handleNumberDeletion = async (numberId: string) => {
    try {
      await deleteData(`${adminDataSource}/number/${numberId}/delete`,getToken());
      await updateNumbers();
    } catch (err) {
      console.log(err);
    }
  };

  const showNumber = (numberId: string) => {
    //Set the visibility of editor

    setAdminEditing(true);
    const userSelectedNumber = numbersInfo.find(
      (number) => number._id === numberId
    );
    if (userSelectedNumber) {
      setSelectedNumber(userSelectedNumber);
    }
  };

  const handleChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const value = evt.target.value;
    const id = evt.target.id;
    setSelectedNumber((prevState) => ({ ...prevState, [id]: value }));
  };

  const handleSubmit = async (evt: React.FormEvent<HTMLFormElement>) => {
    evt.preventDefault();
    const selectedNumberInfo = new FormData();
    selectedNumberInfo.append("numberInfo", JSON.stringify(selectedNumber));
    const apiResponse = await putData(
      `${adminDataSource}/number/${selectedNumber._id}/update`,
      selectedNumberInfo,getToken()
    );
    updateNumbers();
  };

  if (filteredNumbers.length === 0) {
    numberTable = (
      <tbody>
        <tr>
          <td colSpan={8}>No hay numeros para moderar...</td>
        </tr>
      </tbody>
    );
  } else {
    numberTable = (
      <tbody>
        {filteredNumbers.map((number) => {
          return (
            <tr key={number._id} onClick={() => showNumber(number._id)}>
              <td>{number.prefix}</td>
              <td>{number.number}</td>
              <td>{number.location}</td>
              <td>{number.owner}</td>
              <td>{number.whatsapp ? "S" : "No"}</td>
              <td>{number.originWebsite}</td>
              <td>
                <div
                  className="btn_small escort_theme"
                  onClick={() => handleNumberDeletion(number._id)}
                >
                  Eliminar
                </div>
              </td>
            </tr>
          );
        })}
      </tbody>
    );
  }

  return (
    <section className="numbers_moderation">
      <TopNav title="NUMEROS" setActivePage={setActivePage}></TopNav>
      <main className="escort_admin_page">
        <AdminSearch
          originalArray={numbersInfo}
          propToSearch="number"
          setFiltered={setFilteredNumbers}
        />

        <div className="table_container">
          <table className="escorts_table my_sm_16">
            <thead>
              <tr>
                <td>Prefijo</td>
                <td>Numero</td>
                <td>Ubicacin</td>
                <td>Dueo</td>
                <td>WhastApp</td>
                <td>Web de origen</td>
                <td></td>
              </tr>
            </thead>
            {numberTable}
          </table>
        </div>
        <div
          className={`admin_editor ${adminEditing ? "show_container" : "hide"}`}
        >
          <div className="editor_header">
            <h2 className="txt_white">Editar comentario</h2>
            <div
              className="close_editor"
              onClick={() => setAdminEditing(false)}
            >
              <span className="material-symbols-outlined">cancel</span>
            </div>
          </div>
          <form className="escorts_form" onSubmit={handleSubmit}>
            <div className=" input_field_sm">
              <label htmlFor="prefix">Prefijo</label>
              <input
                type="text"
                id="prefix"
                value={selectedNumber.prefix}
                onChange={handleChange}
              />
            </div>
            <div className=" input_field_sm">
              <label htmlFor="number">Numero</label>
              <input
                type="text"
                id="number"
                value={selectedNumber.number}
                onChange={handleChange}
              />
            </div>
            <div className=" input_field_sm">
              <label htmlFor="location">Ubicacion</label>
              <input
                type="text"
                id="location"
                value={selectedNumber.location}
                onChange={handleChange}
              />
            </div>
            <div className=" input_field_sm">
              <label htmlFor="originWebsite">Web de origen</label>
              <input
                type="text"
                id="originWebsite"
                value={selectedNumber.originWebsite}
                onChange={handleChange}
              />
            </div>
            <button className="btn escort_theme">Modificar</button>
          </form>
        </div>
      </main>
    </section>
  );
}
