import { useEffect, useState } from "react";
import { dataSource } from "../api/datasources";
import {fetchData} from "../api/fetchData";
import "../types";



const initializeNumber:NumberInfo = {
_id: "",
comments: [],
prefix: 0,
location: "",
totalSearch:0,
spamReports:0,
owner: "",
lineType:"",
originWebsite:"",
whatsapp:false,
number: "",
}

export default function userLastNumberAdded(number:number) {
    const [lastNumbersInfo, setLastNumbersInfo] = useState([initializeNumber])

    const updateLastNumberAdded = async () => {
        try {
            const apiPromise = await fetchData(`${dataSource}/number/lastAdded/${number}`);
            const apiResponse = await apiPromise.json();
            const response = apiResponse.response as NumberInfo[];
            setLastNumbersInfo(response)

        } catch (err) {
            console.log(err)
        }
    }

    useEffect(() => {
        updateLastNumberAdded()
    }, [])

    return { lastNumbersInfo, updateLastNumberAdded }
}